.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.load {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  height: 600px;
}
.load span span {
  display: block;
}

.txt {
  color: red;
  padding-top: 20px;
}
.theBody {
  margin: 0 auto;
  max-width: 155px;
}

main {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
}
button:focus {
  color: blue;
}
.age p {
  display: flex;
  display: inline-block;
  padding-left: 20px;
}
.sectionAll {
  padding: 20px;
}
